import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import SearchBar from '../SearchBar';
import './FormInput.scss';
import { Link } from 'react-router-dom';

function FormInput({ boolean }) {
  return (
    <Form className="form-container">
      <Row className="mb-3">
        <Form.Group as={Col} controlId="formGridName">
          <Form.Label>Tn</Form.Label>
          <Form.Control type="name" placeholder="Tn ca bn" />
        </Form.Group>

        <Form.Group as={Col} controlId="formGridEmail">
          <Form.Label>Email</Form.Label>
          <Form.Control type="email" placeholder="ex: email@email.com" />
        </Form.Group>
      </Row>

      <Form.Group className="mb-3" controlId="formGridAddress1">
        <Form.Label>Yu cu ca bn</Form.Label>
        <Form.Control placeholder="Nhp  y (nu khng c th  trng)" />
      </Form.Group>

      <Row className="mb-3">
        <Form.Group as={Col} controlId="formGridAge">
          <Form.Label>Tui</Form.Label>
          <Form.Control placeholder="Nhp tui" />
        </Form.Group>

        <Form.Group as={Col} controlId="formGridState">
          <Form.Label>Gii Tnh</Form.Label>
          <Form.Select defaultValue="Choose...">
            <option>Choose...</option>
            <option>Nam</option>
            <option>N</option>
            <option>Khng bit...</option>
          </Form.Select>
        </Form.Group>

        <Form.Group as={Col} controlId="formGridZip">
          <Form.Label>Cung Hong o</Form.Label>
          <Form.Select defaultValue="Choose...">
            <option>Choose...</option>
            <option>Bch Dng (Aries)</option>
            <option>Kim Ngu (Taurus)</option>
            <option>Song T (Gemini) </option>
            <option>C Gii (Cancer)</option>
            <option>S T (Leo)</option>
            <option>X N (Virgo)</option>
            <option>Thin Bnh (Libra)</option>
            <option>Thin Yt (Scorpio)</option>
            <option>Nhn M (Sagittarius)</option>
            <option>Ma Kt (Capricorn)</option>
            <option>Bo Bnh (Aquarius)</option>
            <option>Song Ng (Pisces)</option>
            <option>Khng bit...</option>
          </Form.Select>
        </Form.Group>
      </Row>

      {boolean && (
        <Form.Group controlId="formFileMultiple" className="mb-3">
          <Form.Label>Chn c nhiu nh bn nh!</Form.Label>
          <Form.Control type="file" multiple />
        </Form.Group>
      )}

      <Link to="/weekly-outfit/ur-outfit">
        <Button variant="primary" type="submit">
          Submit
        </Button>
      </Link>
    </Form>
  );
}

export default FormInput;
