import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import '../Register.scss';
import { faFacebook, faGoogle } from '@fortawesome/free-brands-svg-icons';
import { Link } from 'react-router-dom';

function Login() {
  return (
    <div className="Register">
      <div className="form">
        <div className="form-img">
          <h1>Genie</h1>
          <h5>
            "To ti khon ngay v pht huy phong cch ca bn vi Genie!"
          </h5>
          <p>
            "Khm ph th gii phi  tuyt vi vi Genie - ni m phong cch
            ca bn tr nn sng ng v y sng to. Hy to ti khon ngay
            hm nay v khm ph bit bao nhiu cch  ta sng vi phong cch
            ring ca bn!"
          </p>
        </div>
        <div className="form-login">
          <div className="form-header">
            <h1>LOGIN</h1>
          </div>
          <div className="form-body">
            <input type="text" placeholder="User Name" />
            <input type="password" placeholder="Password" />
            <Link className="text" to="/register">
              or register here?
            </Link>
          </div>
          <div className="form-footer">
            <button>Login</button>
            <div className="form-loginApi">
              <a href="/">
                <FontAwesomeIcon icon={faFacebook} />
              </a>
              <a href="/">
                <FontAwesomeIcon icon={faGoogle} />
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Login;
