import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import FormInput from '~/components/Layout/components/FormInput';
import './Weekly.scss';
import React, { useState } from 'react';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import 'owl.carousel/dist/assets/owl.carousel.css';
import Modal from '~/components/Layout/components/ModalB';

const imgs = [
  { srcImg: './images/pages/weekly_pages_1.png', title: '', descr: '' },
];

function WeeklyOutfit() {
  const [key, setKey] = useState('home');
  return (
    <div className="weekly-page">
      <div className="weekly-container">
        <div className="weekly-input">
          <div className="weekly-form">
            <Tabs
              id="controlled-tab-example"
              activeKey={key}
              onSelect={(k) => setKey(k)}
              className="mb-3"
            >
              <Tab eventKey="profile" title="Cha c">
                <div className="weekly-wrapper">
                  <div className="weekly-header">
                    <h1 className="title">Weekly Outfit</h1>
                    <h2>
                      "Get creative with your clothes using our Weekly Outfit
                      feature."
                    </h2>
                    <p>
                      "Cha c" ngha l bn cha c  v bn mun tm mt
                      outfit ngu nhin do chc nng a ra
                    </p>
                  </div>
                  <Container>
                    <Row>
                      <Col>
                        <FormInput />
                      </Col>
                    </Row>
                  </Container>
                </div>
              </Tab>
              <Tab eventKey="home" title="C ri">
                <div className="weekly-wrapper">
                  <div className="weekly-header">
                    <h1 className="title">Weekly Outfit</h1>
                    <h2>
                      "Get creative with your clothes using our Weekly Outfit
                      feature."
                    </h2>
                    <p>
                      <strong>"C ri"</strong> c ngha l bn  c  nhng
                      khng bit phi nh no cho ph hp, sau  chc nng s
                      phi cho bn
                    </p>
                  </div>
                  <Container>
                    <Row>
                      <Col>
                        <FormInput boolean={true} />
                      </Col>
                    </Row>
                  </Container>
                </div>
              </Tab>
            </Tabs>
          </div>
        </div>
        <div className="weekly-card"></div>
      </div>
    </div>
  );
}

export default WeeklyOutfit;
